.item {
  border-radius: 5px;
  color: aqua;
  padding-right: 10px;
  margin: 10px;

  & a {
    display: block;
    width: 100%;
    border-radius: 10px;
    vertical-align: middle;
    padding: 20px;
    color: antiquewhite;
    text-decoration: none;

    &:hover {
      background: #3F8CFF;
    }

    &.active {
      background: #3F8CFF;
    }
  }
}
